.home {
  display: grid;
  place-items: center;
  margin-top: 2%;
  margin-left: 7%;
  margin-right: 7%;
  border-radius: 10px;
  padding: 5%;
  background-image: linear-gradient(
    -45deg,
    #e8bcb9,
    #f39f5a,
    #ae445a,
    #662459,
    #451952,
    #1d1a39
  );
}

#biography {
  display: flex;
  flex: row;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

#profile {
  width: 800px;
  height: 550px;
  border-radius: 10px;
}

#about-header {
  font-size: 50px;
  background: linear-gradient(45deg, #e3af76, #dabd9d, #b69a72);
  background-clip: text;
  color: transparent;
}

#about-me {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 25px;
  color: #1d1a39;
}

#resume {
  background-color: #1d1a39;
  color: #e3af76;
  text-decoration: none;
  border-radius: 10px;
  padding: 10px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 40px;
  transition: all 0.3s ease-in-out;
}

#resume:hover {
  cursor: pointer;
  background-color: #451952;
  color: #dabd9d;
  font-size: 43px;
  padding: 13px;
  transition: all 0.3s ease-in-out;
}

.App footer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 30px;
  background-color: #f39f5a;
}

#whatsapp,
#linkedin {
  width: 50px;
  height: 50px;
  transition: all 0.2s ease-in-out;
}

#whatsapp:hover,
#linkedin:hover {
  width: 55px;
  height: 55px;
  transition: all 0.2s ease-in-out;
}
